body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

* {
  box-sizing: border-box;
}

.mainForm {
  width: 500px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 0 6px 0px #ada9a9;
}

.mainFormControl {
  width: 100%;
  text-align: left;
}

.mainFormControlTitle {
  margin-bottom: 10px;
  display: block;
}

.mainFormControl input {
  margin: 0;
  padding: 0;
  width: 100%;
  padding-left: 10px;
  height: 42px;
  border: 1px solid #ccc;
  border-radius: 3px;
  margin-bottom: 20px;
}

.mainFormSubmit {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  padding-left: 10px;
  border: none;
  padding-top: 10px;
  padding-bottom: 10px;
  box-shadow: none;
  background-color: #337ab7;
  border-color: #2e6da4;
  color: #fff;
  background-image: none;
  border-radius: 4px;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  touch-action: manipulation;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
}
